.outerContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #222831;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #393e46;
  border-radius: 12px;
  height: 60%;
  width: 35%;
  border-style: solid;
  border-color: #eeeeee;
  border-width: 2.5px;
}

@media (min-width: 320px) and (max-width: 480px) {
.outerContainer {
  height: 100%;
}

.container {
  width: 100%;
  height: 100%;
}
}

@media (min-width: 480px) and (max-width: 1200px) {
.container {
  width: 60%;
}
}